// Tools for decoding Basic programs.
import { pad } from "./Utils";
import jss from './Jss';
// Starts at 0x80.
const TOKENS = [
    "END", "FOR", "RESET", "SET", "CLS", "CMD", "RANDOM", "NEXT",
    "DATA", "INPUT", "DIM", "READ", "LET", "GOTO", "RUN", "IF",
    "RESTORE", "GOSUB", "RETURN", "REM", "STOP", "ELSE", "TRON", "TROFF",
    "DEFSTR", "DEFINT", "DEFSNG", "DEFDBL", "LINE", "EDIT", "ERROR", "RESUME",
    "OUT", "ON", "OPEN", "FIELD", "GET", "PUT", "CLOSE", "LOAD",
    "MERGE", "NAME", "KILL", "LSET", "RSET", "SAVE", "SYSTEM", "LPRINT",
    "DEF", "POKE", "PRINT", "CONT", "LIST", "LLIST", "DELETE", "AUTO",
    "CLEAR", "CLOAD", "CSAVE", "NEW", "TAB(", "TO", "FN", "USING",
    "VARPTR", "USR", "ERL", "ERR", "STRING", "INSTR", "POINT", "TIME$",
    "MEM", "INKEY$", "THEN", "NOT", "STEP", "+", "-", "*",
    "/", "[", "AND", "OR", ">", "=", "<", "SGN",
    "INT", "ABS", "FRE", "INP", "POS", "SQR", "RND", "LOG",
    "EXP", "COS", "SIN", "TAN", "ATN", "PEEK", "CVI", "CVS",
    "CVD", "EOF", "LOC", "LOF", "MKI", "MKS$", "MKD$", "CINT",
    "CSNG", "CDBL", "FIX", "LEN", "STR$", "VAL", "ASC", "CHR$",
    "LEFT$", "RIGHT$", "MID$",
];
const REM = 0x93;
const DATA = 0x88;
const REMQUOT = 0xFB;
const ELSE = 0x95;
const EOF = -1;
/**
 * Parser state.
 */
// Normal part of line.
const NORMAL = 0;
// Inside string literal.
const STRING_LITERAL = 1;
// After REM or DATA statement to end of line.
const RAW = 2;
// Just ate a colon.
const COLON = 3;
// Just ate a colon and a REM.
const COLON_REM = 4;
class ByteReader {
    constructor(b) {
        this.b = b;
        this.pos = 0;
    }
    /**
     * Return the next byte, or EOF on end of array.
     *
     * @returns {number}
     */
    read() {
        return this.pos < this.b.length ? this.b[this.pos++] : EOF;
    }
    /**
     * Return the byte address of the next byte to be read.
     */
    addr() {
        return this.pos;
    }
    /**
     * Reads a little-endian short (two-byte) integer.
     *
     * @param allowEofAfterFirstByte
     * @returns the integer, or EOF on end of file.
     */
    readShort(allowEofAfterFirstByte) {
        const low = this.read();
        if (low === EOF) {
            return EOF;
        }
        const high = this.read();
        if (high === EOF) {
            return allowEofAfterFirstByte ? low : EOF;
        }
        return low + high * 256;
    }
}
/**
 *
 * @param out the enclosing element to add to.
 * @param text the text to add.
 * @param className the name of the class for the item.
 */
function add(out, text, className) {
    const e = document.createElement("span");
    e.innerText = text;
    e.classList.add(className);
    out.appendChild(e);
    return e;
}
// Stylesheet.
const BACKGROUND_COLOR = "#1E1E1E";
const STYLE = {
    error: {
        color: "#aa0000",
        "&$highlighted": {
            backgroundColor: "#aa0000",
            color: BACKGROUND_COLOR,
        },
    },
    lineNumber: {
        color: "#858585",
        "&$highlighted": {
            backgroundColor: "#858585",
            color: BACKGROUND_COLOR,
        },
    },
    punctuation: {
        color: "#D4D4D4",
        "&$highlighted": {
            backgroundColor: "#D4D4D4",
            color: BACKGROUND_COLOR,
        },
    },
    keyword: {
        color: "#C586C0",
        "&$highlighted": {
            backgroundColor: "#C586C0",
            color: BACKGROUND_COLOR,
        },
    },
    regular: {
        color: "#9CDCFE",
        "&$highlighted": {
            backgroundColor: "#9CDCFE",
            color: BACKGROUND_COLOR,
        },
    },
    string: {
        color: "#CE9178",
        "&$highlighted": {
            backgroundColor: "#CE9178",
            color: BACKGROUND_COLOR,
        },
    },
    comment: {
        color: "#6A9955",
        "&$highlighted": {
            backgroundColor: "#6A9955",
            color: BACKGROUND_COLOR,
        },
    },
    selected: {
        backgroundColor: "#555555",
    },
    highlighted: {
    // Empty style that's referenced above as $highlighted.
    },
};
const sheet = jss.createStyleSheet(STYLE);
export const highlightClassName = sheet.classes.highlighted;
export const selectClassName = sheet.classes.selected;
/**
 * Decode a tokenized Basic program.
 * @param bytes tokenized program.
 * @param out div to write result into.
 * @return array of generated HTML elements, index by byte index.
 */
export function fromTokenized(bytes, out) {
    sheet.attach();
    const classes = sheet.classes;
    const b = new ByteReader(bytes);
    let state;
    const elements = [];
    if (b.read() !== 0xD3 || b.read() !== 0xD3 || b.read() !== 0xD3) {
        add(out, "Basic: missing magic -- not a BASIC file.", classes.error);
        return elements;
    }
    // One-byte ASCII program name. This is nearly always meaningless, so we do nothing with it.
    b.read();
    while (true) {
        const line = document.createElement("div");
        // Read the address of the next line. We ignore this (as does Basic when
        // loading programs), only using it to detect end of program. (In the real
        // Basic these are regenerated after loading.)
        const address = b.readShort(true);
        if (address === EOF) {
            add(line, "[EOF in next line's address]", classes.error);
            break;
        }
        // Zero address indicates end of program.
        if (address === 0) {
            break;
        }
        // Read current line number.
        const lineNumber = b.readShort(false);
        if (lineNumber === EOF) {
            add(line, "[EOF in line number]", classes.error);
            break;
        }
        let e = add(line, lineNumber.toString(), classes.lineNumber);
        elements[b.addr() - 2] = e;
        e = add(line, " ", classes.regular);
        elements[b.addr() - 1] = e;
        // Read rest of line.
        let c; // Uint8 value.
        let ch; // String value.
        state = NORMAL;
        while (true) {
            c = b.read();
            if (c === EOF || c === 0) {
                break;
            }
            ch = String.fromCharCode(c);
            // Detect the ":REM'" sequence (colon, REM, single quote), because
            // that translates to a single quote. Must be a backward-compatible
            // way to add a single quote as a comment.
            if (ch === ":" && state === NORMAL) {
                state = COLON;
            }
            else if (ch === ":" && state === COLON) {
                e = add(line, ":", classes.punctuation);
                elements[b.addr() - 1] = e;
            }
            else if (c === REM && state === COLON) {
                state = COLON_REM;
            }
            else if (c === REMQUOT && state === COLON_REM) {
                e = add(line, "'", classes.comment);
                elements[b.addr() - 1] = e;
                state = RAW;
            }
            else if (c === ELSE && state === COLON) {
                e = add(line, "ELSE", classes.keyword);
                elements[b.addr() - 1] = e;
                state = NORMAL;
            }
            else {
                if (state === COLON || state === COLON_REM) {
                    e = add(line, ":", classes.punctuation);
                    elements[b.addr() - 1] = e;
                    if (state === COLON_REM) {
                        e = add(line, "REM", classes.comment);
                        elements[b.addr() - 1] = e;
                        state = RAW;
                    }
                    else {
                        state = NORMAL;
                    }
                }
                switch (state) {
                    case NORMAL:
                        if (c >= 128 && c < 128 + TOKENS.length) {
                            const token = TOKENS[c - 128];
                            e = add(line, token, c === DATA || c === REM ? classes.comment
                                : token.length === 1 ? classes.punctuation
                                    : classes.keyword);
                        }
                        else {
                            e = add(line, ch, ch === '"' ? classes.string : classes.regular);
                        }
                        elements[b.addr() - 1] = e;
                        if (c === DATA || c === REM) {
                            state = RAW;
                        }
                        else if (ch === '"') {
                            state = STRING_LITERAL;
                        }
                        break;
                    case STRING_LITERAL:
                        if (ch === "\r") {
                            e = add(line, "\\n", classes.punctuation);
                        }
                        else if (ch === "\\") {
                            e = add(line, "\\" + pad(c, 8, 3), classes.punctuation);
                        }
                        else if (c >= 32 && c < 128) {
                            e = add(line, ch, classes.string);
                        }
                        else {
                            e = add(line, "\\" + pad(c, 8, 3), classes.punctuation);
                        }
                        elements[b.addr() - 1] = e;
                        if (ch === '"') {
                            // End of string.
                            state = NORMAL;
                        }
                        break;
                    case RAW:
                        e = add(line, ch, classes.comment);
                        elements[b.addr() - 1] = e;
                        break;
                }
            }
        }
        if (c === EOF) {
            add(line, "[EOF in line]", classes.error);
            break;
        }
        // Deal with eaten tokens.
        if (state === COLON || state === COLON_REM) {
            e = add(line, ":", classes.punctuation);
            elements[b.addr() - 1] = e;
            if (state === COLON_REM) {
                e = add(line, "REM", classes.comment);
            }
            elements[b.addr() - 1] = e;
            /// state = NORMAL;
        }
        // Append last line.
        out.appendChild(line);
    }
    return elements;
}
